import Navbar from "@/components/Navbar";

export default {
    data(){
        return{
            // pageTitle:'首页',           
        }
    },
    computed:{
        pageTitle:function(){
            let nowRoute = this.$route.path;
            if(nowRoute == '/home/index'){
                return '首页'
            }
            if(nowRoute == '/home/member'){
                return '个人中心'
            }
            else{
                return 'mcjisok'
            }
        }
    },
    components:{
        Navbar,
    },
    methods:{
        
        show:function(){
            // console.log(this.a)
            this.$router.push('/home/index')
        },
        routerJump:function(){
            this.$router.push('/home/push')            
        }
    },
    mounted:function () {
        this.show();
    }
}



