
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Comment from '@/components/Home/Comment'
export default {
name:'pushlist',
data () {
return {
lilength:[1,2,3],

page:0,
total:0,
pushlist:[],
hasMore:true,
GET_PUSHLIST_API:this.HOST.host + '/getpushlist',

loadmoreSW:true,
host:this.HOST.host
}
},



components: {
Comment
},
methods: {
logIndexChange (arg) {
console.log(arg)
},
show (index) {

console.log(index)
this.$refs.previewer.show(index)
},
loadmore(){

console.log(this.page)
if(this.hasMore){
this.page ++;
let _this = this;
this.$http.post(this.GET_PUSHLIST_API,{
page:_this.page
})
.then(res=>{
setTimeout(() => {

_this.loadmoreSW = true
_this.total = res.data.toal
_this.hasMore = res.data.hasMore
_this.pushlist = _this.pushlist.concat(res.data.pushList)
}, 300);

})
.catch(err=>{

})
}
else{




this.loadmoreSW = !this.loadmoreSW
}
}
},
mounted:function () {
this.loadmore();
let _this = this;

this.loadmoreSW = true;
window.addEventListener('scroll',function(){



if(document.body.scrollTop + window.innerHeight >= document.body.offsetHeight && _this.hasMore) {


if(_this.loadmoreSW){


_this.loadmoreSW = false;
_this.loadmore();
}
}
})
}

}



