import { mapActions } from 'vuex'
export default {
    data(){
        return{
            username:'',
            userpwd:'',
            required:true,
            login_url:this.HOST.host + '/login'
        }
    },
    methods:{
        ...mapActions([
            'changeLogin'
        ]),
        test:function(){
            // console.log('1')
            this.$http.get(''+this.HOST.host+'/ajaxtest')
                .then(response => {  
                    console.log("获取信息成功");  
                    console.log(response);  
                }, response => {  
                    console.log("获取信息失败");  
                    console.log(response);  
            })  
        },
        login:function(){
            // console.log('点击登录按钮有反应')
            let _self = this;
            this.$http.post(''+this.HOST.host+'/login',{
                username:this.username,
                userpassword:this.userpwd
            })
            .then(res=>{
                let status = res.data.code;
                let msg = res.data.msg;
                console.log(1111111111111111111111111)
                switch (status) {
                    case 1:
                        _self.$vux.toast.show({
                            text: msg,
                            type:'warn'
                        })
                        break;
                    case 2:
                        _self.$vux.toast.show({
                            text: '登录成功',
                            type:'success',
                            onHide () {
                                console.log('登录成功之后改变state.loginuserid'+res.data)
                                // _self.changeLogin()
                                console.log(res.data)
                                _self.$store.dispatch('changeLogin',res.data)
                                _self.$router.push('/home')
                            }
                        })
                        break;
                    case 3:
                        _self.$vux.toast.show({
                            text: msg,
                            type:'warn'
                        })
                        break;
                }
            })
            .catch(err=>{
                console.log(21)
            })
        }
    },
    mounted:function () {
        this.test();
    }
}



