export default {
    data(){
        return{
            name:'',
            username:'',
            userpwd:'',
            required:true,
            reg_url: this.HOST.host + '/register',
        }
    },
    methods:{
        register:function(){
            let _self = this;
            if(this.username !== '' && this.userpwd !== ''){
                this.$http.post(this.reg_url,{
                    // name:this.name,
                    username:this.username,
                    userpassword:this.userpwd
                })
                .then(res => {
                    // console.log(res)
                    if(res.data.code === 200){
                        this.$vux.alert.show({
                            title: '恭喜您注册成功！',
                            content: '确定(将前往登录页面)',
                            onShow () {
                            },
                            onHide () {
                                _self.$router.push('/login')
                            }
                        })
                    }
                    else if(res.data.code === 404){
                        this.$vux.toast.show({
                            text: '用户已存在！',
                            type:'warn'
                        })
                    }
                }), res => {  
                    console.log("获取信息失败");  
                    console.log(res);  
                }            
            }
            else{
                this.$vux.toast.show({
                    text: '请输入用户名或密码',
                    type:'cancel'
                })
            }
            
        }
    }
}



